<template>
  <div class="component" >
    <h2>{{ scopedStrings.title }}</h2>
    <table class="status_list">
      <tr class="status" v-for="stat in status" :key="stat.id">
        <td class="label">{{ stat.name }}</td>
        <td class="value">{{ stat.value }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  export default {
    props: {
      scopedStrings: {
        type: Object,
        required: true
      },
      initialValue: {
        required: true
      }
    },
    data () {
      return {
        status: this.initialValue
      }
    },
    methods: {
      update (newValue) {
        this.status = newValue;
      }
    }
  }
</script>

<style scoped>
  .component table {
    border-collapse: collapse;
  }
</style>
