<template>

  <Dialog
    ref="Dialog"
    :title="this.scopedStrings.title"
    :width=300
  >

    <div class="save-body">
      <div class="choice-container">
        <div class="choice" v-for="(item, index) in items" :key="index">
          <input type="checkbox" :id="item.id" :value="item.id" v-model="saveItems">
          <label :for="item.id">{{ item.label }}</label>
        </div>
      </div>

      <button :disabled="emptySaveList" @click="saveAll">
        <i class="fa fa-save"></i>
        {{ this.scopedStrings.title }}
      </button>
    </div>

  </Dialog>

</template>

<script>
import Dialog from "@/components/Dialog";

export default {
  props: {
    scopedStrings: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      saveItems: []
    }
  },
  computed: {
    emptySaveList() {
      return this.saveItems.length == 0
    },
    items() {
      return [{
        id: "editor",
        label: this.scopedStrings.editor
      }, {
        id: "execution",
        label: this.scopedStrings.machineState
      }, {
        id: "instructionSet",
        label: this.scopedStrings.instructionSet
      }, {
        id: "hcl",
        label: this.scopedStrings.hcl
      }]
    }
  },
  components: {
    Dialog
  },
  methods: {
    show () {
      this.saveItems = [this.$parent.getCurrentTab().name]
      this.$refs.Dialog.show()
    },
    saveAll() {
      this.$parent.appSave(this.saveItems)
      this.$refs.Dialog.hide()
    }
  }
}

</script>

