  transition-duration: .3s;
}

button:hover {
  background-color: var(--SaveDialog-button-hover-background);
}

button:disabled {
  background-color: var(--SaveDialog-button-disabled-background);
  color: var(--SaveDialog-button-disabled-foreground);
