  }

  .component > .title-bar > h1 {
    flex-grow: 1;
  }

  .title-bar > button {
    background-color: var(--Editor-button-enabled-background);
    color: var(--Editor-button-enabled-foreground);
    margin: 4px 0 4px 0;
    width: 28px;
  }

  .title-bar > button:hover {
    background-color: var(--Editor-button-hover-background);
  }

  .title-bar > button:disabled {
    cursor: not-allowed;
    background-color: var(--Editor-button-disabled-background);
    color: var(--Editor-button-disabled-foreground);
  }

  #editor-reset-size {
    margin-right:4px;
  }

  .title-bar > input[type=number] {
    border: solid 0px;
    color: var(--Editor-input-foreground);
    background-color: var(--Editor-input-background);
    font-size: 12pt;
    margin: 4px 0 4px 0;
    text-align: center;
    width: 48px;
  }

  /**
   * https://stackoverflow.com/questions/45396280/customizing-increment-arrows-on-input-of-type-number-using-css
   * Used to hide native arrows on input of type number.
   */
  input[type="number"] {
    -webkit-appearance: textfield;
      -moz-appearance: textfield;
            appearance: textfield;
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
  }

</style>
