  }

  .object-code-line .highlightedV7 {
    background-color: var(--ObjectCode-highlighted-background-7);
    width: 100%;
  }

  .object-code-line .highlightedV8 {
    background-color: var(--ObjectCode-highlighted-background-8);
    width: 100%;
  }

  .object-code-line .highlightedV9 {
    background-color: var(--ObjectCode-highlighted-background-9);
